.work-listing {
	.mixin-dynamic-font-size(1.125rem,1.5rem,@to-viewport: 43.75rem);
	list-style-type: none;
	
	margin: 1em 0;
	padding: 0;
}

.work-listing__item {
	display: flex;
	align-items: center;
	
	padding: 1em 0;
	
	border: 1px solid @bg-color;
	border-width: 0 0 1px 0;
	
	&:first-child {
		border-width: 1px 0;
	}

	@media screen and (max-width: 37rem) {
		flex-direction: column;
	}
}

.work-listing__visual {
	width: 12.5em;
	height: auto;
	
	@media screen and (max-width: 37rem) {
		width: 34%;
	}
}

.work-listing__heading {
	.mixin-dynamic-font-size(1.25rem,2rem,@to-viewport: 43.75rem);
	margin: 0;
	
	font-weight: 700;

	color: @secondary-color;
}


.work-listing__link {
	margin-top: 1em;
	margin-left: 12.5em;
		
	@media screen and (max-width: 37rem) {
		margin-left: 0;
	}
}